<template>
    <div>
        <mt-header fixed title="文章详情">
            <router-link to="/" slot="left">
                <mt-button icon="back">返回</mt-button>
            </router-link>
        </mt-header>
        <div class="index-container">
            <div class="content" v-html="newsDetail.body"></div>
        </div>
    </div>
</template>

<script>
    import { Toast } from 'mint-ui';
    import axios from 'axios';
    import { Indicator } from 'mint-ui';
    import { ZHIHU_API_HOST } from '../config';

    export default {
        name: 'NewsDetail',
        data() {
            return {
                loading: false,
                newsDetail: {
                    body: '',
                },
            }
        },
        mounted() {
            const { news } = this.$route.params;
            if (news) {
                this.getNewsDetail(news.id);
            } else {
                this.$router.replace({ name: 'Index' });
            }
        },
        methods: {
            getNewsDetail(id) {
                Indicator.open();
                axios.get(`${ZHIHU_API_HOST}/news/${id}`).then((response) => {
                    if (response.status === 200) {
                        const { body } = response.data;
                        this.newsDetail.body = this.replaceImageUrl(body);
                        Indicator.close();
                    }
                }).catch((error) => {
                    console.log(error);
                    Indicator.close();
                    Toast(error.response.data.error.message);
                });
            },
            replaceImageUrl(str) {
                const imgReg = /<img.*?(?:>|\/>)/gi;
                const path = `${ZHIHU_API_HOST}/simulateRefer?image=`;
                str = str.replace(imgReg, (imgSrc) => {
                    imgSrc = imgSrc.replace(/src=(?:"\s*([^"]*)\s*"|'\s*([^']*)\s*'|(\S+))/i,"src=\""+path+"$1\"");
                    return imgSrc;
                });
                return str;
            },
        },
    }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
    .index-container{position: fixed;left: 0;top:40px;right: 0;bottom: 0;overflow: auto;}
</style>



